import { Injectable, NgZone } from '@angular/core';
import { AngularFireAuth } from "@angular/fire/auth";
import * as firebase from 'firebase/app';
import { Router } from '@angular/router';
import { JwtHelperService } from '@auth0/angular-jwt';
import { AngularFirestore, AngularFirestoreDocument, AngularFirestoreCollection } from '@angular/fire/firestore';
import { UserKey } from '../pages/home/upload-keys/UserKey.model';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  userData: any;
  notesCollection
  uploadedKeys: boolean;
  constructor(
    public firebase: AngularFireAuth,
    public router: Router,
    public ngZone: NgZone,
    public jwtHelper: JwtHelperService,
    public afs: AngularFirestore,
  ) {

    this.notesCollection = this.afs.collection(`users`);

  }




  updateUserData(keys) {
    const userRef: AngularFirestoreDocument<UserKey> = this.afs.doc(`service-accounts/${JSON.parse(localStorage.getItem('user_uid'))}`);
    const data = {
      auth_provider_x509_cert_url: keys.auth_provider_x509_cert_url,
      auth_uri: keys.auth_uri,
      client_email: keys.client_email,
      client_id: keys.client_id,
      client_x509_cert_url: keys.client_x509_cert_url,
      private_key: keys.private_key,
      private_key_id: keys.private_key_id,
      project_id: keys.project_id,
      token_uri: keys.token_uri,
      type: keys.type
    }
    return userRef.set(data, { merge: true })
  }
  isUploadedKeys() {
    console.log(localStorage.getItem('user_uid'))
    return this.afs.doc("service-accounts/" + JSON.parse(localStorage.getItem('user_uid'))).snapshotChanges()
  }
  verifyEmail() {
    this.firebase.authState.subscribe((user) => {
      if (user) {
        this.userData = user;
        user.sendEmailVerification()
        localStorage.setItem('user', JSON.stringify(this.userData));
        localStorage.setItem('user_uid', JSON.stringify(this.userData.uid));
        JSON.parse(localStorage.getItem('user'));
      } else {
        localStorage.setItem('user', null);
        JSON.parse(localStorage.getItem('user'));
      }
    })
    return true
  }
  SignUp(email: string, password: string) {
    return this.firebase.createUserWithEmailAndPassword(email, password)
      .then((res) => {
        console.log(res)
        if (res["message"] != undefined)
          return res
        return this.verifyEmail()
      }).catch(error => error)
  }
  GoogleAuth() {
    localStorage.removeItem('user');
    localStorage.removeItem('user_uid');
    localStorage.removeItem('user_avatar');
    localStorage.removeItem('user_name');
    return this.firebase.signInWithPopup(new firebase.auth.GoogleAuthProvider()).then((data) => {
      if (data['operationType'] == 'signIn') {
        this.firebase.authState.subscribe((user) => {
          if (user) {
            this.userData = user;
            console.log(user)
            localStorage.setItem('user', JSON.stringify(this.userData));
            JSON.parse(localStorage.getItem('user'));
            localStorage.setItem('user_uid', JSON.stringify(this.userData.uid));
            localStorage.setItem('user_name', JSON.stringify(this.userData.displayName));
            localStorage.setItem('user_email', JSON.stringify(this.userData.email));

            this.router.navigate(['/home']);

          } else {
            localStorage.setItem('user', null);
            JSON.parse(localStorage.getItem('user'));
          }
        })
      }
    });
  }

  SignIn(email: string, password: string) {
    localStorage.removeItem('user');
    localStorage.removeItem('user_uid');
    localStorage.removeItem('user_email');
    localStorage.removeItem('user_name');
    return this.firebase.signInWithEmailAndPassword(email, password)
      .then((result) => {
        console.log(result, result["message"])
        if (result['user']["emailVerified"] !== true) {
          this.verifyEmail()
          return result
        } else {
          this.firebase.authState.subscribe((user) => {
            if (user) {
              this.userData = user;
              console.log(user)

              localStorage.setItem('user', JSON.stringify(this.userData));
              JSON.parse(localStorage.getItem('user'));
              localStorage.setItem('user_uid', JSON.stringify(this.userData.uid));
              localStorage.setItem('user_email', JSON.stringify(this.userData.email));

              this.router.navigate(['/home']);

            } else {
              localStorage.setItem('user', null);
              JSON.parse(localStorage.getItem('user'));
            }
          })
          return result
        }
      }).catch((error) => error)
  }
  getUserAvatar() {
    return localStorage.getItem('user_avatar')

  }
  SignOut() {
    return this.firebase.signOut().then(() => {
      localStorage.removeItem('user');
      localStorage.removeItem('user_uid');
      localStorage.removeItem('user_email');
      localStorage.removeItem('user_name');
      this.router.navigate(['sign-in']);
    })

  }
  getUserName() {
    return localStorage.getItem('user_name')
  }
  getUserEmail() {
    return localStorage.getItem('user_email')
  }
  isAuthenticated(): boolean {
    const token = localStorage.getItem('user');
    if (token) return true
    return false
  }


}
