import { Component, OnInit, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { PopoverController } from '@ionic/angular';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-settings-popover',
  templateUrl: './settings-popover.component.html',
  styleUrls: ['./settings-popover.component.scss'],
})
export class SettingsPopoverComponent implements OnInit {
  email: string;



  constructor(private router: Router,
    public auth: AuthService,
    public popoverController: PopoverController,
    @Inject(DOCUMENT) private document: Document
  ) {



  }

  onClick(event) {
    let systemDark = window.matchMedia("(prefers-color-scheme: dark)");
    systemDark.addListener(this.colorTest);
    if (event.detail.checked) {

      this.document.body.classList.add('dark');

      document.body.setAttribute('data-theme', 'dark');
    }
    else {
      this.document.body.classList.remove('dark');

      document.body.setAttribute('data-theme', 'light');
    }
  }

  colorTest(systemInitiatedDark) {
    if (systemInitiatedDark.matches) {
      document.body.setAttribute('data-theme', 'dark');
    } else {
      document.body.setAttribute('data-theme', 'light');
    }
  }
  ngOnInit() {
    this.email = JSON.parse(this.auth.getUserEmail())

  }
  signOut() {

    this.auth.SignOut()
    this.popoverController.dismiss();

  }
  close() {
    this.popoverController.dismiss();

  }
  uploadKeysPage() {
    this.router.navigateByUrl("/home/upload-keys")
    this.popoverController.dismiss();

  }
}
